.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navBarDiv {
  height: '40px';
  width: '100%';
  display: 'flex';
  justify-content: 'center';
  background-color: '#fff';
  color: #000;
}

.navBarButton {
  margin-bottom: 3px;
  padding: 10px 20px;
  height: 30px;
  background-color: #fff;
  text-decoration: none;
  border-radius: 2px;
  width: 150px;
  border: 1px solid black;
  color: black;
}

.aboutMeSection {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}
